import { useStaticQuery, graphql } from 'gatsby';

export const useSiteData = () => {
  const { site } = useStaticQuery<GatsbyTypes.SiteDataQuery>(
    graphql`
      query SiteData {
        site {
          siteMetadata {
            title
            siteUrl
            description
            author
          }
          buildTime(formatString: "YYYY")
        }
      }
    `,
  );
  return site;
};

export default useSiteData;
