import { faBars, faTimes } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Transition } from '@headlessui/react';
import clsx from 'clsx';
import { Link } from 'gatsby';
import React, { useState } from 'react';

import superblyBanner from '../images/superbly-logo.svg';

interface SiteLink {
  route: string;
  title: string;
}

const LINKS: SiteLink[] = [
  {
    route: `/products/`,
    title: `Products`,
  },
  {
    route: `/news/`,
    title: `News`,
  },
  {
    route: `/about/`,
    title: `About`,
  },
];

interface Props {
  siteTitle?: string;
}

const Header: React.FC<Props> = ({ siteTitle = '' }) => {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <header className="relative px-4 py-6 sm:px-6 md:justify-start md:space-x-10">
      <div className="max-w-screen-xl mx-auto px-4 sm:px-6">
        <nav className="relative flex items-center justify-between sm:h-10 md:justify-center">
          <div className="flex items-center flex-1 md:absolute md:inset-y-0 md:left-0">
            <div className="flex items-center justify-between w-full md:w-auto">
              <Link to="/" aria-label="Home">
                <img className="h-8 w-auto sm:h-10" src={superblyBanner} alt={siteTitle} />
              </Link>
              <div className="-mr-2 flex items-center md:hidden">
                <button
                  type="button"
                  className="inline-flex items-center justify-center p-2 rounded-md text-gray-400 hover:text-gray-500 hover:bg-gray-100 focus:outline-none focus:bg-gray-100 focus:text-gray-500 transition duration-150 ease-in-out"
                  id="main-menu"
                  aria-label="Main menu"
                  aria-haspopup="true"
                  onClick={() => setIsOpen(!isOpen)}
                >
                  <FontAwesomeIcon icon={faBars} size="lg" />
                </button>
              </div>
            </div>
          </div>
          <div className="hidden md:flex md:space-x-10">
            {LINKS.map((link) => (
              <Link
                className="font-medium text-gray-500 hover:text-gray-900 transition duration-150 ease-in-out"
                key={link.title}
                to={link.route}
              >
                {link.title}
              </Link>
            ))}
          </div>
        </nav>
      </div>

      {/* Mobile menu, show/hide based on menu open state. */}
      <Transition
        className="absolute top-0 inset-x-0 p-2 transition transform origin-top-right md:hidden"
        show={isOpen}
        enter="duration-150 ease-out"
        enterFrom="opacity-0 scale-95"
        enterTo="opacity-100 scale-100"
        leave="duration-100 ease-in"
        leaveFrom="opacity-100 scale-100"
        leaveTo="opacity-0 scale-95"
      >
        <div className="rounded-lg shadow-lg">
          <div
            className="rounded-lg shadow-xs bg-white"
            role="menu"
            aria-orientation="vertical"
            aria-labelledby="main-menu"
          >
            <div className="px-5 pt-4 flex items-center justify-between">
              <div>
                <img className="h-8 w-auto" src={superblyBanner} alt={siteTitle} />
              </div>
              <div className="-mr-2">
                <button
                  type="button"
                  className="inline-flex items-center justify-center p-2 rounded-md text-gray-400 hover:text-gray-500 hover:bg-gray-100 focus:outline-none focus:bg-gray-100 focus:text-gray-500 transition duration-150 ease-in-out"
                  aria-label="Close menu"
                  onClick={() => setIsOpen(!isOpen)}
                >
                  <FontAwesomeIcon icon={faTimes} size="lg" />
                </button>
              </div>
            </div>
            <div className="px-2 pt-2 pb-3">
              {LINKS.map((link, i) => (
                <Link
                  className={clsx(
                    i > 0 && 'mt-1',
                    'block px-3 py-2 rounded-md text-base font-medium text-gray-700 hover:text-gray-900 hover:bg-gray-50 focus:outline-none focus:text-gray-900 focus:bg-gray-50 transition duration-150 ease-in-out',
                  )}
                  key={link.title}
                  to={link.route}
                  role="menuitem"
                >
                  {link.title}
                </Link>
              ))}
            </div>
          </div>
        </div>
      </Transition>
    </header>
  );
};

export default Header;
